import { AbstractControl, ValidationErrors, ValidatorFn, Validators } from '@angular/forms';
import { AddVehicleForm } from 'src/app/model/chq-new-case-form.model';
import { CustomValidationResult } from 'src/app/model/chq-validation-model';
import { ChqWidgetsHeaderModel } from 'src/app/model/chq-widgets-header-model';

export const newVehicleForm = (): AddVehicleForm => {
  const model = {
    vin: {
      placeHolder: 'Search VIN',
      name: 'vin',
      label: 'Vin Number',
      type: 'text',
      value: 0,
      icon: 'search',
      maxLength: 17,
      validation: {
        name: 'vin',
        validationFunction: (): ValidatorFn => {
          return Validators.compose([ Validators.minLength(17), Validators.pattern('^[a-hA-Hj-nJ-NpPr-zR-Z0-9]+$') ])
        },
        validationMessage: (error: ValidationErrors): string => {
          if (error['pattern']) {
            return 'Invalid characters'
          }
          if (error['minlength']) {
            return 'Vin should have 17 characters'
          }
          return '';
        }
      }
    },
    make: {
      placeHolder: 'Make',
      name: 'make',
      label: 'Make',
      options: [ {} ],
      displayValue: 'name',
      fieldValue: 'name',
      type: 'select',
      value: 0,
      validation: {
        name: 'make',
        validationFunction: (): ValidatorFn => {
          return Validators.compose([ Validators.required ])
        },
        validationMessage: (error: ValidationErrors): string => {
          if (error['required']) {
            return 'Please select make'
          }
          return '';
        }
      },
    },
    model: {
      placeHolder: 'Model',
      name: 'model',
      label: 'Model',
      options: [ {} ],
      displayValue: 'modelDescription',
      fieldValue: 'modelDescription',
      type: 'select',
      value: 0,
      validation: {
        name: 'model',
        validationFunction: (): ValidatorFn => {
          return Validators.compose([ Validators.required ])
        },
        validationMessage: (error: ValidationErrors): string => {
          if (error['required']) {
            return 'Please select model'
          }
          return '';
        }
      }
    },
    year: {
      placeHolder: 'Import Interval',
      label: 'Import Interval',
      name: 'year',
      //selectedOption: '',
      options: [ {} ],
      displayValue: 'name',
      fieldValue: 'name',
      type: 'select',
      value: 0,
      validation: {
        name: 'year',
        validationFunction: (): ValidatorFn => {
          return Validators.compose([ Validators.required ])
        },
        validationMessage: (error: ValidationErrors): string => {
          if (error['required']) {
            return 'Please select year'
          }
          return '';
        }
      }
    },
    licensePlate: {
      placeHolder: 'License Plate',
      label: 'License Plate',
      name: 'licensePlate',
      value: 0,
      type: 'text',
      maxLength: 12,
      validation: {
        name: 'licensePlate',
        validationFunction: (): ValidatorFn => {
          return Validators.compose([ Validators.required, Validators.maxLength(12), Validators.pattern('^[a-zA-Z0-9]*$'), licensePlateValidator() ])
        },
        validationMessage: (error: ValidationErrors): string => {
          if (error['required']) {
            return 'Please enter license plate'
          }
          if (error['maxlength']) {
            return 'License plate should have not more than 12 characters'
          }
          if (error['pattern']) {
            return 'Only alphanumberic characters are allowed'
          }
          if (error['invalidLicensePlate']) {
            return 'License plate should not be 0'
          }
          return '';
        }
      }
    },
    carMilege: {
      placeHolder: 'Car Mileage',
      label: 'Car Mileage',
      name: 'carMilege',
      value: 0,
      type: 'text',
      maxLength: 7,
      validation: {
        name: 'carMilege',
        validationFunction: (): ValidatorFn => {
          return Validators.compose([ Validators.required, Validators.maxLength(7), Validators.pattern('^[0-9]*$') ])
        },
        validationMessage: (error: ValidationErrors): string => {
          if (error['required']) {
            return 'Please enter car mileage'
          }
          if (error['maxlength']) {
            return 'Car mileage should have not more than 7 characters'
          }
          if (error['pattern']) {
            return 'Only numeric characters are allowed'
          }
          if (error['invalidCarMileage']) {
            return 'Car mileage should be greater than 0'
          }
          return '';
        }
      }
    },
    registrationDate: {
      placeHolder: 'Registration date',
      label: 'Registration date',
      name: 'registrationDate',
      value: '',
      type: 'date',
      maxDate: new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate() - 1),
      minDate: new Date('1980-01-01'),
      validation: {
        name: 'registrationDate',
        validationFunction: (): ValidatorFn => {
          return Validators.compose([ Validators.required ])
        },
        validationMessage: (error: ValidationErrors): string => {
          if (error['required']) {
            return 'Please enter date'
          }
          return '';
        }
      }
    },
    manufacturingDate: {
      placeHolder: 'Manufacturing  date',
      label: 'Manufacturing  date',
      name: 'manufacturingDate',
      value: '',
      type: 'date',
      maxDate: new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate() - 1),
      minDate: new Date('1980-01-01'),
      validation: {
        name: 'manufacturingDate',
        validationFunction: (): ValidatorFn => {
          return Validators.compose([ Validators.required ])
        },
        validationMessage: (error: ValidationErrors): string => {
          if (error['required']) {
            return 'Please enter date'
          }
          return '';
        }
      }
    },
  }
  return model;
};

export const newVehicleHeader: ChqWidgetsHeaderModel = {
  title: 'Add vehicle',
  step: 2,
  description: 'Add your customers vehicle below',
  stepName: 'Step Two',
};

/**
 *
 * @returns
 */
export function carMileageValidator(): ValidatorFn {
  const result: CustomValidationResult = { 'invalidCarMileage': false };
  return (control: AbstractControl): ValidationErrors | null => {
    if (control.value && control.value < 1) {
      result['invalidCarMileage'] = true;
      return result;
    }
    return null;
  }
}

/**
 * License plate Validator
 * @returns
 */
export function licensePlateValidator(): ValidatorFn {
  const result: CustomValidationResult = { 'invalidLicensePlate': false };
  return (control: AbstractControl): ValidationErrors | null => {
    if (control.value === '0') {
      result['invalidLicensePlate'] = true;
      return result;
    }
    return null;
  }
}
